@import "./base.scss";

.coolLink {
    position: relative;
    display: grid;
    place-items: center;
    overflow: hidden;

    &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: currentColor;
        transform: scale3d(0, 1, 1);
        transition: transform 0.4s cubic-bezier(.5, .5, .3, 1);
        transform-origin: right;
    }

    &:after {
        content: attr(data-text);
        top: 0;
        height: 100%;
        position: absolute;
        transform: translate3d(150%, 0, 0);
        transition: transform 0.4s cubic-bezier(.5, .5, .3, 1);
    }

    & span {
        transition: transform 0.4s cubic-bezier(.5, .5, .3, 1);
    }

    &:hover:before {
        transform: scale3d(1, 1, 1);
        transform-origin: left;
    }

    &:hover:after {
        transform: translate3d(0, 0, 0);
    }

    &:hover span {
        transform: translate3d(-150%, 0, 0);
    }
}

.coolButton {
    position: relative;
    border: none;
    cursor: pointer;
    width: 175px;
    height: 120px;
    background: none;
    color: #fff;
    &:before,
    &:after {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &:before {
        content: "";
        background: #fff;
        clip-path: path("M154.5,88.5 C131,113.5 62.5,110 30,89.5 C-2.5,69 -3.5,42 4.5,25.5 C12.5,9 33.5,-6 85,3.5 C136.5,13 178,63.5 154.5,88.5 Z");
        transition: clip-path 0.5s cubic-bezier(0.585, 2.5, 0.645, 0.55);
    }

    &:hover:before {
        clip-path: path("M143,77 C117,96 74,100.5 45.5,91.5 C17,82.5 -10.5,57 5.5,31.5 C21.5,6 79,-5.5 130.5,4 C182,13.5 169,58 143,77 Z");
    }

    & span {
        mix-blend-mode: difference;
        font-weight: 500;
    }

    &:after {
        content: "";
        height: 85%;
        width: 98%;
        top: 6px;
        border: 1px solid #fff;
        border-radius: 58% 42% 55% 45% / 56% 45% 55% 44%;
        transform: rotate(-20deg);
        transition: transform 0.5s cubic-bezier(0.585, 2.5, 0.645, 0.55);
    }

    &:hover:after {
        transform: translate3d(0, -5px, 0);
    }

    @media (max-width: 800px) {
        & {
            transform: scale3d(0.7, 0.7, 0.7);
            font-size: 23px;
        }
    }
}

.coloredIcon {
    fill: #fff;
    width: 35px;
    height: 35px;

    &:hover {
        animation: coloredIconAnimation 0.3s infinite;
    }

    @keyframes coloredIconAnimation {
        0% {
            filter: drop-shadow(5px 4px 2px $red-color);
        }

        25% {
            filter: drop-shadow(-5px -6px 2px $blue-color);
        }

        50% {
            filter: drop-shadow(5px -4px 2px $red-color);
        }

        75% {
            filter: drop-shadow(-8px -4px 2px $blue-color);
        }

        100% {
            filter: drop-shadow(-5px 0px 2px $red-color);
        }
    }
}